import { Component } from '@angular/core';

import { User } from '../_models/index';

@Component({
    template: require('./home.component.html')
})

export class HomeComponent {
    currentUser: User;

    constructor() {
        this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
    }
}



